<template>
<div class="container row">
  <main class="container col">
    <br>
    <h1 class="display-6 text-center">Gachi++에 오신것을 환영합니다!</h1>
    <hr>
    <article >
      <p class="lead">
        "가치++는 누구든 시간과 노력을 들여 자신이 정한 목표를 이룰 수 있도록 
          연결을 도와주는 소셜 네트워크 입니다. 
          개인의 목표가 우리의 목표가 되어 더 큰 가치를 만들어 낼 수 있습니다."
        </p>
        <br>
      <figure class="text-end">
        <blockquote class="blockquote">
          <p class="lead fs-6">"성공한 사람이 아니라 가치있는 사람이 되기 위해 힘쓰라" <br>
            <span class="fs-7">"Try not to become a man of success but rather try to become a man of value"</span></p>
        </blockquote>
        <figcaption class="blockquote-footer"> 알버트 아인슈타인 <cite title="Source Title">Albert Einstein</cite>
        </figcaption>
      </figure>
    </article>
  </main>

  <div class="col tuto position-relative">
    <h3 style="color:aliceblue">가입하면 더 많은 그룹을 볼 수 있고, 만들수도 있어요. 
      버튼을 눌러 그룹을 상세보기 해보세요</h3>
    <RouterLink to="/" style="text-decoration:none; color:#30353d;" >
          <button type="button" class="btn btn-light btn-lg position-absolute top-0 end-0" style="background:rgba(6, 90, 92, 0.3);border-radius: 2em;">X</button>
      </RouterLink>
    <div class="row">
    <div class="card border-success mb-3 " style="max-width: 16rem;" v-for="(list, i) in state.lists" :key="(list, i)">
      <h5 class="card-header">
        <RouterLink to="/tutoviewcircle"  style="text-decoration:none;color:rgba(0,0,0,.6)">{{list.name}}</RouterLink>
        <span class="fs-5">&nbsp&nbsp{{list.memberCount}}/{{list.maxNum}}</span><img alt="user icon" src="../assets/icones/people.jpg">
      </h5>
      <div class="card-body ">
        <p class="card-text ellipsis">{{list.content}}</p>
        <h7 class="card-title">최근 활동 :&nbsp{{formatDate(list.modDate)}}</h7>
        <p class="card__apply">
          <a class="card__link " href="#" v-if="list.pass != ''" style="font-size:large">지금 가입</a>
          <a class="card__link" href="#" v-else style="font-size:large">지금 가입 </a>
        </p>
      </div>
    </div>
    </div>
    <RouterLink to="/tutoviewcircle" style="text-decoration:none; color:#30353d;padding-left: 18em;" >
      <button type="button" class="btn btn-success" style="background:rgba(6, 90, 92, 0.5);border-radius: 2em;">
        <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" fill="currentColor" class="bi bi-arrow-return-right" viewBox="0 0 16 16">
          <path fill-rule="evenodd" d="M1.5 1.5A.5.5 0 0 0 1 2v4.8a2.5 2.5 0 0 0 2.5 2.5h9.793l-3.347 3.346a.5.5 0 0 0 .708.708l4.2-4.2a.5.5 0 0 0 0-.708l-4-4a.5.5 0 0 0-.708.708L13.293 8.3H3.5A1.5 1.5 0 0 1 2 6.8V2a.5.5 0 0 0-.5-.5z"></path>
        </svg> <span class="fs-1">그룹 보기</span> 
      </button>
    </RouterLink>
  </div>
</div>
</template>
<script>
import { onMounted } from 'vue'
import { reactive,ref } from '@vue/reactivity'
import axios from 'axios'
import dayjs from 'dayjs'
import { useRouter } from 'vue-router'
export default {
  setup() {
  onMounted(() => { handleGetList() })
  let chbox = ref(null)
  const router = useRouter();
  const state = reactive({
      lists       : '',
      circleid    :'',
      name        :'',
      pass        :'',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
  })
  const modal = reactive({
      id          :'',
      name        :'',
      maxNum      :'',
      memberCount :''
  })
  const sendId = (id, name, maxNum, memberCount) =>{
      modal.id = id
      modal.name = name
      modal.maxNum = maxNum
      modal.memberCount = memberCount
  }
  const handleGetList = async () => {
      console.log("handleGetList launch")
      const url = 'http://43.201.60.125/api/tutocirclelist'
      const headers = {
      'Content-Type': 'application/json',
      Authorization: state.token,
      token: state.token,
      }
      const body = { email:  'nolaboda11@ds.org' }
      await axios.post(url, body, { headers }).then(function (res) {
      if (res.status === 200) {
          state.lists = res.data
      }
      })
  }
  const formatDate = (dateString) => {
          const date = dayjs(dateString);
          return date.format('YY년 MM월 D일');
  }
  const join = async ( )=>{
      if(modal.memberCount>=modal.maxNum){
      alert("too many")
      }else{
      const url = 'http://43.201.60.125/api/joinCircle'
      const headers = {
          'Content-Type': 'application/json',
          Authorization: state.token,
          token: state.token,
      }
      const body = { circleid: modal.id, email : state.email }
      await axios.post(url, body, { headers }).then(function (res) {
          if (res.status === 200) {
          alert("Joined "+res.data );router.push({name: "privateProfile"});
          }
      }).catch(function (error){
          if(error){
          console.log('Password is not correct from Join');
          }
      })
      }
  }
  const passCheck = async (circleid)=>{
      if(modal.memberCount>=modal.maxNum){
      alert("too many")
      }else{
      const url = 'http://43.201.60.125/api/passCheck'
      const headers = {
          'Content-Type': 'application/json',
          Authorization: state.token,
          token: state.token,
      }
      const body = { circleid: circleid, pass:state.pass }
      await axios.post(url, body, {headers}).then(function (res){
          if (res.data.dto.pass == state.pass){
          state.pass = res.data.dto.pass
          state.circleid = res.data.dto.circleid
          join(res.data.dto.circleid);
          } else{alert("not correct");}
      }).catch(function(error){
          if(error){
          alert('password is incorrect from passCheck!')
          }
      })
      }
  }
  
  return { state, handleGetList, formatDate,join,passCheck,sendId,modal,chbox}
  },
}
</script>
<style scoped>
.tuto {
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	/* background: linear-gradient(-45deg, #065A5c, #D9D9D9); */
	background-size: 200% 200%;
	animation: gradient 10s ease infinite;
  border-radius: 1em;
  padding: 3em;
}
.ellipsis {
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.card {
  border: solid black 1px;
  margin-left: 15px;
  margin-right: 15px;
  padding: 5px;
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}
.card:hover {
  /* box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4); */
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.4);
  transform: scale(1.01);
}
.card__link,
.card__exit,
.card__icon {
  position: relative;
  text-decoration: none;
  color: rgba(6, 90, 92);
  font-weight: 500;
  /* color: rgba(6, 90, 92, 0.9); */
}
.card__link::after {
  position: absolute;
  top: 25px;
  left: 0;
  content: "";
  width: 0%;
  height: 3px;
  background-color: rgba(6, 90, 92, 0.7);
  transition: all 0.5s;
}
.card__link:hover::after {
  width: 100%;
}
.card__apply {
  grid-row: 5/5;
  align-self: center;
}
img {
  width: 1.5em;
}
.filter-green{
filter: invert(26%) sepia(23%) saturate(2821%) hue-rotate(143deg) brightness(90%) contrast(95%);
    }
</style>